<template>
  <div class="sortList" v-loading="loading">
    <div class="header">
      <h2 style="flex: 1"></h2>
      <el-button type="primary" @click="resetOrder"></el-button>
      <el-button type="success" @click="saveOrder"></el-button>
    </div>
    <draggable
      class="list-group"
      tag="transition-group"
      :component-data="{
        tag: 'ul',
        type: 'transition-group',
        name: !drag ? 'flip-list' : null,
      }"
      v-model="objArray"
      v-bind="dragOptions"
      @start="drag = true"
      @end="drag = false"
      item-key="objectId"
    >
      <template #item="{ element }">
        <div class="cart card list-group-item">
          <el-card
            shadow="hover"
            class="box-card"
            :body-style="{ padding: '5px' }"
          >
            <el-row justify="space-between">
              <el-col :span="8">
                <el-image
                  style="width: 5rem; height: 5rem"
                  :src="element.img[0].url"
                  fit="cover"
              /></el-col>
              <el-col :span="16">
                <div class="right-box">
                  <h3>{{ element.name }}</h3>
                </div>
              </el-col>
            </el-row>
          </el-card>
        </div>
      </template>
    </draggable>
  </div>
</template>

<script>
import AV from "@/service/init.js";
import draggable from "vuedraggable";
import ItemService from "@/service/itemService.js";
import { ElMessage } from "element-plus";
// import Card from "@/components/Card.vue";
export default {
  components: {
    draggable,
    // Card,
  },
  data() {
    return {
      drag: false,
      objArray: [],
      loading: false,
      myArray: [{ name: "zzy", id: 1 }],
    };
  },

  watch: {
    myArray(newArr, oldArr) {
      console.log(newArr);
    },
  },
  methods: {
    async getObjs() {
      this.loading = true;
      this.objArray = await ItemService.getItems();
      this.loading = false;
      // console.log(this.objArray);
      // this.displayedItems = this.filterItems;
      //   console.log(this.objArray);
    },

    saveOrder() {
      // order
      for (let i = 0; i < this.objArray.length; i++) {
        this.objArray[i].order = i;
      }
      // 
      this.updateOrder();
    },
    resetOrder() {
      this.objArray.sort((a, b) => {
        return a.order - b.order;
      });
    },

    updateOrder() {
      let updates = [];
      for (let item of this.objArray) {
        const newObj = AV.Object.createWithoutData("item", item.objectId);
        newObj.set("order", item.order);
        updates.push(newObj);
      }

      // 
      AV.Object.saveAll(updates).then(
        function (savedObjects) {
          //  resolve savedObjects  AV.Object 
          console.log("");
          ElMessage({
            showClose: true,
            type: "success",
            message: "",
          });
        },
        function (error) {
          //  reject 
        }
      );
      //   Promise.all(updates).then(() => {
      //     console.log("");
      //   });
    },
  },
  computed: {
    dragOptions() {
      return {
        animation: 200,
        group: "description",
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },

  created() {
    this.getObjs();
  },
};
</script>

<style scoped>
.header {
  display: flex;
  padding: 0.5rem;
  background-color: #f4f4f5;
  /* text-align: left; */
}

.button {
  margin-top: 35px;
}
.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
.list-group {
  min-height: 20px;
}
.list-group-item {
  cursor: move;
}
.list-group-item i {
  cursor: pointer;
}
</style>
